/**
 * Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄ Ïì∞Í∏∞
 * @param key
 * @param value
 */
export function setStorage(key: string, value: any): void {
  value = JSON.stringify(value);
  localStorage[key] = value;
}

/**
 * Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄ ÏùΩÍ∏∞
 * @param key
 */
export function getStorage(key: string): any {
  let value = localStorage[key];
  if (!value) {
    console.info(`localStorageÏóê ${key}Îäî ÏóÜÏäµÎãàÎã§.`);
    return null;
  }

  try {
    value = JSON.parse(value);
  } catch (error) {
    console.error('getStorage catch(error)', error);
  }

  return value;
}

/**
 * Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄ ÌäπÏ†ï key Îç∞Ïù¥ÌÑ∞ ÏÇ≠Ï†ú
 * @param key
 */
export function deleteStorage(key: string): void {
  localStorage.removeItem(key);
}
